.header{
  background: #000;
  min-height: 100vh;
  color: #fff;
  display: flex;

  .logo{
    font-size: 40px;
    font-weight: $bold-weight;
    letter-spacing: 3px;

    span{
      position: relative;

      &:after{
        content: "";
        display: inline-block;
        position: absolute;
        width: 100%;
        height: 6px;
        left: 0;
        background: green;
        top: calc(50% - 3px)
      }
    }
  }

  .nav{
    border: 1px solid red;
    padding: 20px 0;
    ul{
      justify-content: flex-end;
      li{
        border: 1px solid green;
      }
    }
  }
}